<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="songname" >
					<el-input v-model="ruleForm.songname" placeholder="" clearable  :readonly="ro.songname"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="songname" >
					<el-input v-model="ruleForm.songname" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="singer" >
					<el-input v-model="ruleForm.singer" placeholder="" clearable  :readonly="ro.singer"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="singer" >
					<el-input v-model="ruleForm.singer" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="gequfenlei" >
					<el-select :disabled="ro.gequfenlei" v-model="ruleForm.gequfenlei" placeholder="" >
						<el-option
							v-for="(item,index) in gequfenleiOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="gequfenlei" >
					<el-input v-model="ruleForm.gequfenlei"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.fengmian" label="" prop="fengmian" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
						@change="fengmianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.fengmian" label="" prop="fengmian" >
					<img v-if="ruleForm.fengmian.substring(0,4)=='http'&&ruleForm.fengmian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.fengmian" width="100" height="100">
					<img v-else-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="bianqu" >
					<el-input v-model="ruleForm.bianqu" placeholder="" clearable  :readonly="ro.bianqu"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="bianqu" >
					<el-input v-model="ruleForm.bianqu" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="tianci" >
					<el-input v-model="ruleForm.tianci" placeholder="" clearable  :readonly="ro.tianci"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="tianci" >
					<el-input v-model="ruleForm.tianci" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yuyan" >
					<el-input v-model="ruleForm.yuyan" placeholder="" clearable  :readonly="ro.yuyan"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yuyan" >
					<el-input v-model="ruleForm.yuyan" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="zhuanji" >
					<el-input v-model="ruleForm.zhuanji" placeholder="" clearable  :readonly="ro.zhuanji"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="zhuanji" >
					<el-input v-model="ruleForm.zhuanji" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shizhang" >
					<el-input v-model="ruleForm.shizhang" placeholder="" clearable  :readonly="ro.shizhang"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shizhang" >
					<el-input v-model="ruleForm.shizhang" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="faxingfang" >
					<el-input v-model="ruleForm.faxingfang" placeholder="" clearable  :readonly="ro.faxingfang"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="faxingfang" >
					<el-input v-model="ruleForm.faxingfang" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="faxingriqi" >
					<el-date-picker
						format="yyyy  MM  dd "
						value-format="yyyy-MM-dd"
						v-model="ruleForm.faxingriqi" 
						type="date"
						:readonly="ro.faxingriqi"
						placeholder=""
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.faxingriqi" label="" prop="faxingriqi" >
					<el-input v-model="ruleForm.faxingriqi" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info'&& !ro.songfile" label="" prop="songfile" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="1"
						:type="3"
						:multiple="true"
						:fileUrls="ruleForm.songfile?ruleForm.songfile:''"
						@change="songfileUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item v-else-if="ruleForm.songfile" label="" prop="songfile" >
					<el-button class="viewBtn" type="text" size="small" @click="download($base.url+ruleForm.songfile)">
						<span class="icon iconfont icon-chakan14"></span>
						
					</el-button>
				</el-form-item>
				<el-form-item v-else-if="!ruleForm.songfile" label="" prop="songfile" >
					<el-button class="unBtn" type="text" size="small">
						<span class="icon iconfont icon-xihuan"></span>
						
					</el-button>
				</el-form-item>
			</template>
			<el-form-item v-if="type!='info'"  label="" prop="geci" >
				<editor 
					style="min-width: 200px; max-width: 600px;"
					v-model="ruleForm.geci" 
					class="editor"
					myQuillEditor="geci"
					action="file/upload">
				</editor>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.geci" label="" prop="geci" >
				<span class="text ql-snow ql-editor" v-html="ruleForm.geci"></span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isNumber,
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isNumber(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					songname : false,
					singer : false,
					gequfenlei : false,
					fengmian : false,
					bianqu : false,
					tianci : false,
					yuyan : false,
					zhuanji : false,
					shizhang : false,
					faxingfang : false,
					faxingriqi : false,
					geci : false,
					songfile : false,
					clicktime : false,
					clicknum : false,
					discussnum : false,
					totalscore : false,
					storeupnum : false,
				},
			
				ruleForm: {
					songname: '',
					singer: '',
					gequfenlei: '',
					fengmian: '',
					bianqu: '',
					tianci: '',
					yuyan: '',
					zhuanji: '',
					shizhang: '',
					faxingfang: '',
					faxingriqi: '',
					geci: '',
					songfile: '',
					clicktime: '',
				},
				gequfenleiOptions: [],

				rules: {
					songname: [
						{ required: true, message: '', trigger: 'blur' },
					],
					singer: [
						{ required: true, message: '', trigger: 'blur' },
					],
					gequfenlei: [
						{ required: true, message: '', trigger: 'blur' },
					],
					fengmian: [
					],
					bianqu: [
					],
					tianci: [
					],
					yuyan: [
					],
					zhuanji: [
					],
					shizhang: [
					],
					faxingfang: [
					],
					faxingriqi: [
					],
					geci: [
					],
					songfile: [
						{ required: true, message: '', trigger: 'blur' },
					],
					clicktime: [
					],
					clicknum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					discussnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					totalscore: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.faxingriqi = this.getCurDate()
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='songname'){
							this.ruleForm.songname = obj[o];
							this.ro.songname = true;
							continue;
						}
						if(o=='singer'){
							this.ruleForm.singer = obj[o];
							this.ro.singer = true;
							continue;
						}
						if(o=='gequfenlei'){
							this.ruleForm.gequfenlei = obj[o];
							this.ro.gequfenlei = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o];
							this.ro.fengmian = true;
							continue;
						}
						if(o=='bianqu'){
							this.ruleForm.bianqu = obj[o];
							this.ro.bianqu = true;
							continue;
						}
						if(o=='tianci'){
							this.ruleForm.tianci = obj[o];
							this.ro.tianci = true;
							continue;
						}
						if(o=='yuyan'){
							this.ruleForm.yuyan = obj[o];
							this.ro.yuyan = true;
							continue;
						}
						if(o=='zhuanji'){
							this.ruleForm.zhuanji = obj[o];
							this.ro.zhuanji = true;
							continue;
						}
						if(o=='shizhang'){
							this.ruleForm.shizhang = obj[o];
							this.ro.shizhang = true;
							continue;
						}
						if(o=='faxingfang'){
							this.ruleForm.faxingfang = obj[o];
							this.ro.faxingfang = true;
							continue;
						}
						if(o=='faxingriqi'){
							this.ruleForm.faxingriqi = obj[o];
							this.ro.faxingriqi = true;
							continue;
						}
						if(o=='geci'){
							this.ruleForm.geci = obj[o];
							this.ro.geci = true;
							continue;
						}
						if(o=='songfile'){
							this.ruleForm.songfile = obj[o];
							this.ro.songfile = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='totalscore'){
							this.ruleForm.totalscore = obj[o];
							this.ro.totalscore = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.$http({
					url: `option/gequfenlei/gequfenlei`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.gequfenleiOptions = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `gequxinxi/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
						this.ruleForm.geci = this.ruleForm.geci.replace(reg,'../../../springboot53084988/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.fengmian!=null) {
						this.ruleForm.fengmian = this.ruleForm.fengmian.replace(new RegExp(this.$base.url,"g"),"");
					}
					if(this.ruleForm.songfile!=null) {
						this.ruleForm.songfile = this.ruleForm.songfile.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `gequxinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.gequxinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.gequxinxiCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls;
			},
			songfileUploadChange(fileUrls) {
				this.ruleForm.songfile = fileUrls;
			},
		}
	};
</script>
<style lang="scss" scoped>
	.addEdit-block {
		padding: 30px;
	}
	.add-update-preview {
		padding: 40px 25% 40px 10%;
		background: none;
		border-color: #eee;
		border-width: 0px 0 0;
		border-style: solid;
	}
	.amap-wrapper {
		width: 100%;
		height: 500px;
	}
	
	.search-box {
		position: absolute;
	}
	
	.el-date-editor.el-input {
		width: auto;
	}
	.add-update-preview /deep/ .el-form-item {
		border: 0px solid #eee;
		padding: 0;
		margin: 0 0 26px 0;
		display: inline-block;
		width: 100%;
	}
	.add-update-preview .el-form-item /deep/ .el-form-item__label {
		padding: 0 10px 0 0;
		color: #6e6e6e;
