<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="geshouxingming" >
					<el-input v-model="ruleForm.geshouxingming" placeholder="" clearable  :readonly="ro.geshouxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="geshouxingming" >
					<el-input v-model="ruleForm.geshouxingming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yiming" >
					<el-input v-model="ruleForm.yiming" placeholder="" clearable  :readonly="ro.yiming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yiming" >
					<el-input v-model="ruleForm.yiming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="guoji" >
					<el-input v-model="ruleForm.guoji" placeholder="" clearable  :readonly="ro.guoji"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="guoji" >
					<el-input v-model="ruleForm.guoji" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="chushengriqi" >
					<el-date-picker
						format="yyyy  MM  dd "
						value-format="yyyy-MM-dd"
						v-model="ruleForm.chushengriqi" 
						type="date"
						:readonly="ro.chushengriqi"
						placeholder=""
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.chushengriqi" label="" prop="chushengriqi" >
					<el-input v-model="ruleForm.chushengriqi" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="chushengdi" >
					<el-input v-model="ruleForm.chushengdi" placeholder="" clearable  :readonly="ro.chushengdi"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="chushengdi" >
					<el-input v-model="ruleForm.chushengdi" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="yinleliupai" >
					<el-select :disabled="ro.yinleliupai" v-model="ruleForm.yinleliupai" placeholder="" >
						<el-option
							v-for="(item,index) in yinleliupaiOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yinleliupai" >
					<el-input v-model="ruleForm.yinleliupai"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="jingjigongsi" >
					<el-input v-model="ruleForm.jingjigongsi" placeholder="" clearable  :readonly="ro.jingjigongsi"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="jingjigongsi" >
					<el-input v-model="ruleForm.jingjigongsi" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="chudaoshijian" >
					<el-date-picker
						format="yyyy  MM  dd "
						value-format="yyyy-MM-dd"
						v-model="ruleForm.chudaoshijian" 
						type="date"
						:readonly="ro.chudaoshijian"
						placeholder=""
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.chudaoshijian" label="" prop="chudaoshijian" >
					<el-input v-model="ruleForm.chudaoshijian" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="zuopinlianjie" >
					<el-input v-model="ruleForm.zuopinlianjie" placeholder="" clearable  :readonly="ro.zuopinlianjie"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="zuopinlianjie" >
					<el-input v-model="ruleForm.zuopinlianjie" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.touxiang" label="" prop="touxiang" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
						@change="touxiangUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.touxiang" label="" prop="touxiang" >
					<img v-if="ruleForm.touxiang.substring(0,4)=='http'&&ruleForm.touxiang.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.touxiang" width="100" height="100">
					<img v-else-if="ruleForm.touxiang.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.touxiang.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.touxiang.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
			</template>
			<el-form-item class="textarea" v-if="type!='info'" label="" prop="daibiaozuopin" >
				<el-input
					style="min-width: 200px; max-width: 600px;"
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.daibiaozuopin" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.daibiaozuopin" label="" prop="daibiaozuopin" >
				<span class="text">{{ruleForm.daibiaozuopin}}</span>
			</el-form-item>
			<el-form-item class="textarea" v-if="type!='info'" label="" prop="huojiangjilu" >
				<el-input
					style="min-width: 200px; max-width: 600px;"
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.huojiangjilu" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.huojiangjilu" label="" prop="huojiangjilu" >
				<span class="text">{{ruleForm.huojiangjilu}}</span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isNumber,
		isIntNumer,
		isURL,
	} from "@/utils/validate";
	export default {
		data() {
			var validateUrl = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isURL(value)) {
					callback(new Error("URL"));
				} else {
					callback();
				}
			};
			var validateNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isNumber(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					geshouxingming : false,
					yiming : false,
					guoji : false,
					chushengriqi : false,
					chushengdi : false,
					yinleliupai : false,
					jingjigongsi : false,
					chudaoshijian : false,
					daibiaozuopin : false,
					huojiangjilu : false,
					zuopinlianjie : false,
					touxiang : false,
					discussnum : false,
					totalscore : false,
					storeupnum : false,
				},
			
				ruleForm: {
					geshouxingming: '',
					yiming: '',
					guoji: '',
					chushengriqi: '',
					chushengdi: '',
					yinleliupai: '',
					jingjigongsi: '',
					chudaoshijian: '',
					daibiaozuopin: '',
					huojiangjilu: '',
					zuopinlianjie: '',
					touxiang: '',
				},
				yinleliupaiOptions: [],

				rules: {
					geshouxingming: [
						{ required: true, message: '', trigger: 'blur' },
					],
					yiming: [
						{ required: true, message: '', trigger: 'blur' },
					],
					guoji: [
					],
					chushengriqi: [
					],
					chushengdi: [
					],
					yinleliupai: [
					],
					jingjigongsi: [
					],
					chudaoshijian: [
					],
					daibiaozuopin: [
					],
					huojiangjilu: [
					],
					zuopinlianjie: [
						{ validator: validateUrl, trigger: 'blur' },
					],
					touxiang: [
					],
					discussnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					totalscore: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.chushengriqi = this.getCurDate()
			this.ruleForm.chudaoshijian = this.getCurDate()
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='geshouxingming'){
							this.ruleForm.geshouxingming = obj[o];
							this.ro.geshouxingming = true;
							continue;
						}
						if(o=='yiming'){
							this.ruleForm.yiming = obj[o];
							this.ro.yiming = true;
							continue;
						}
						if(o=='guoji'){
							this.ruleForm.guoji = obj[o];
							this.ro.guoji = true;
							continue;
						}
						if(o=='chushengriqi'){
							this.ruleForm.chushengriqi = obj[o];
							this.ro.chushengriqi = true;
							continue;
						}
						if(o=='chushengdi'){
							this.ruleForm.chushengdi = obj[o];
							this.ro.chushengdi = true;
							continue;
						}
						if(o=='yinleliupai'){
							this.ruleForm.yinleliupai = obj[o];
							this.ro.yinleliupai = true;
							continue;
						}
						if(o=='jingjigongsi'){
							this.ruleForm.jingjigongsi = obj[o];
							this.ro.jingjigongsi = true;
							continue;
						}
						if(o=='chudaoshijian'){
							this.ruleForm.chudaoshijian = obj[o];
							this.ro.chudaoshijian = true;
							continue;
						}
						if(o=='daibiaozuopin'){
							this.ruleForm.daibiaozuopin = obj[o];
							this.ro.daibiaozuopin = true;
							continue;
						}
						if(o=='huojiangjilu'){
							this.ruleForm.huojiangjilu = obj[o];
							this.ro.huojiangjilu = true;
							continue;
						}
						if(o=='zuopinlianjie'){
							this.ruleForm.zuopinlianjie = obj[o];
							this.ro.zuopinlianjie = true;
							continue;
						}
						if(o=='touxiang'){
							this.ruleForm.touxiang = obj[o];
							this.ro.touxiang = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='totalscore'){
							this.ruleForm.totalscore = obj[o];
							this.ro.totalscore = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.yinleliupaiOptions = ",,,".split(',')
			
			},
			// 

			info(id) {
				this.$http({
					url: `geshouxinxi/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.touxiang!=null) {
						this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `geshouxinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.geshouxinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.geshouxinxiCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			touxiangUploadChange(fileUrls) {
				this.ruleForm.touxiang = fileUrls;
			},
		}
	};
</script>
<style lang="scss" scoped>
